import {Component} from '@angular/core';
import {LugaresService} from '../service/lugar.service';
import {ActivatedRoute} from '@angular/router';
import {Observable, observable} from 'rxjs';
import 'rxjs/Rx';
import {FormControl} from '@angular/forms';
import {HttpClient} from '@angular/common/http';
import {debounceTime, map, switchMap} from 'rxjs/operators';

@Component({
  selector: 'app-crear',
  templateUrl: './crear.component.html'
})
export class CrearComponent {

  lugar: any = {};
  id: any = null;

  private searchField: FormControl;
  result$: Observable<any>;

  constructor(private lugaresService: LugaresService, private route: ActivatedRoute, private http: HttpClient) {
    this.id = this.route.snapshot.params['id'];
    if (this.id !== 'new') {
      this.lugaresService.getLugar(this.id)
        .subscribe((lugar) => {
          console.log(lugar);
          this.lugar = lugar;
        });
    }

    const URL_API_GOOGLE = 'https://maps.googleapis.com/maps/api/geocode/json';
    const API_KEY_GOOGLE = 'AIzaSyC3Svnu5-FcGqz8mJhpEOHZApMfBUH9Ips';

    this.searchField = new FormControl();
    this.result$ = this.searchField.valueChanges.pipe(debounceTime(300),
      switchMap(query => this.http.get(`${URL_API_GOOGLE}?address= ${query}&key=${API_KEY_GOOGLE}`)),
      map( (response: any) => response.results)
    );
  }

  guardarLugar() {
    var direccion = this.lugar.calle + ',' + this.lugar.ciudad + ',' + this.lugar.pais;
    this.lugaresService.obtenerGeoData(direccion).subscribe((resultado: any) => {
      this.lugar.lat = resultado.results[0].geometry.location.lat;
      this.lugar.lng = resultado.results[0].geometry.location.lng;

      if (this.id !== 'new') {
        this.lugaresService.editarLugar(this.lugar);
        alert('Negocio editado con éxito');
      } else {
        this.lugar.id = Date.now();
        this.lugaresService.guardarLugar(this.lugar);
        alert('Negocio guardado con éxito');
      }
      this.lugar = {};
    });
  }

  seleccionarDireccion(direccion) {
    console.log(direccion);
    this.lugar.calle = direccion.address_components[0].long_name + ' ' + direccion.address_components[1].long_name;
    this.lugar.ciudad = direccion.address_components[2].long_name;
    this.lugar.pais = direccion.address_components[4].long_name;
  }
}

