import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { AgmCoreModule } from '@agm/core';
import {ResaltarDirective} from './directives/resaltar.directive';
import {ContarClicksDirective} from './directives/contar-clicks.directive';
import {RouterModule, Routes} from '@angular/router';
import {DetalleComponent} from './detalle/detalle.component';
import {LugaresComponent} from './lugares/lugares.component';
import {ContactoComponent} from './contacto/contacto.component';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {LugaresService} from './service/lugar.service';
import {AngularFireModule} from '@angular/fire';
import { environment } from '../environments/environment';
import {CrearComponent} from './crear/crear.component';
import {AngularFireDatabaseModule} from '@angular/fire/database';
import {HttpClientModule} from '@angular/common/http';

import { registerLocaleData } from '@angular/common';
import localeEs from '@angular/common/locales/es';
import localeEsExtra from '@angular/common/locales/extra/es';
import {LinkifystrPipe} from './pipe/linkifystr.pipe';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { LoginComponent } from './login/login.component';
import { RegistroComponent } from './registro/registro.component';
import {AutorizacionService} from './service/autorizacion.service';
import {AngularFireAuth, AngularFireAuthModule} from '@angular/fire/auth';
import {MyGuard} from './service/my-guard.service';

// the second parameter 'es' is optional
registerLocaleData(localeEs, 'es-MX', localeEsExtra);


// TODO Paso 1-25: Se crea la constante para inicializar nuestra diferentes componentes (rutas/vistas) (video=25)
const appRoutes: Routes = [
  {path: '', component: LugaresComponent},
  {path: 'lugares', component: LugaresComponent},
  // TODO Paso 5-25: Le agregamos una ruta a nuestro componente
  {path: 'detalle/:id', component: DetalleComponent},
  {path: 'contacto', component: ContactoComponent},
  {path: 'crear/:id', component: CrearComponent, canActivate: [MyGuard]},
  {path: 'login', component: LoginComponent},
  {path: 'registro', component: RegistroComponent},
  {path: '**', component: LugaresComponent}];

@NgModule({
  declarations: [
    AppComponent,
    ResaltarDirective,
    ContarClicksDirective,
    // TODO Paso 4-25: Es importante agregar la declaracion de nuestro componente
    DetalleComponent,
    LugaresComponent,
    ContactoComponent,
    CrearComponent,
    LinkifystrPipe,
    LoginComponent,
    RegistroComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyC3Svnu5-FcGqz8mJhpEOHZApMfBUH9Ips'
    }),
    // TODO Paso 2-25: Para que nuestra app trabaje como más de una ruta se exportan nuestros componentes (video=25)
    RouterModule.forRoot(appRoutes),
    FormsModule,
    AngularFireModule.initializeApp(environment.firebase),
    AngularFireDatabaseModule,
    AngularFireAuthModule,
    HttpClientModule,
    BrowserAnimationsModule,
    ReactiveFormsModule
  ],
  providers: [LugaresService, AutorizacionService, MyGuard],
  bootstrap: [AppComponent]
})
export class AppModule {
}
